import React, { useState, useEffect } from 'react';
import { useTheme } from '@mui/material/styles';
import {
    Button,
    FormControl,
    Grid,
    MenuItem,
    Select,
    TextField,
    CircularProgress,
    useMediaQuery,
    Dialog,
    DialogActions,
    DialogTitle,
    DialogContent
} from '@mui/material';
import { Controller, useForm } from 'react-hook-form';
import AnimateButton from 'ui-component/extended/AnimateButton';
import firebase from '../../frebase/firebase';
import moment from 'moment';
import { InputLabel } from '@mui/material';
import OutlinedInput from '@mui/material/OutlinedInput';
import { getDownloadURL, ref, uploadBytes, getStorage } from 'firebase/storage';
import { useNavigate } from 'react-router';
import PhoneInput from 'react-phone-input-2';
import 'react-phone-input-2/lib/material.css';

const SubForm = ({ ...others }) => {
    const [loading, setLoading] = useState(false);
    const [topics, setTopics] = useState([]);
    const [showModal, setShowModal] = useState(false);
    const [seasons, setSeasons] = useState([]);
    const [infoError, setInfoError] = useState(false);
    const [img, setImg] = useState('');
    const [imgError, setImgError] = useState(false);
    const [seasonIdError, setSeasonIdError] = useState(false);
    const [topicsError, setTopicsError] = useState(false);
    const [seasonId, setSeasonId] = useState('');
    const [selectedTopics, setSelectedTopics] = useState([]);
    const [phoneNumber, setPhoneNumber] = useState('');

    const theme = useTheme();
    const navigate = useNavigate();
    const matchDownSM = useMediaQuery(theme.breakpoints.down('md'));
    const {
        handleSubmit,
        formState: { errors },
        control,
        watch
    } = useForm({
        defaultValues: {
            title: '',
            firstName: '',
            lastName: '',
            description: '',
            prizeMoney: '',
            entryFee: '',
            email: '',
            phone: ''
        },
        mode: 'all'
    });

    const onSubmit = async (leagueData) => {
        try {
            if (selectedTopics.length === 0) {
                setTopicsError(true);
            } else if (seasonId.length === 0) {
                setSeasonIdError(true);
            } else if (img.length === 0) {
                setImgError(true);
            } else if (watch('firstName') === '' || watch('lastName') === '' || watch('email') === '' || watch('phone') === '') {
                setInfoError(true);
            } else {
                const id = moment().valueOf();
                setLoading(true);
                leagueData.createdAt = moment().valueOf();
                leagueData.id = id.toString();
                leagueData.isRejected = false;
                leagueData.SeasonDetail = seasons.filter((season) => season.id === seasonId)[0];
                leagueData.ContactInfo = {
                    firstName: leagueData.firstName,
                    lastName: leagueData.lastName,
                    email: leagueData.email,
                    phone: leagueData.phone
                };
                leagueData.genre = selectedTopics;
                leagueData.quizzes = [];
                leagueData.registerationMonth = [];
                leagueData.subscribes = [];
                // upload img now
                const storageRef = ref(getStorage(), `/QuizLeague/${img.name}`);
                const imgSnapshot = await uploadBytes(storageRef, img);
                const url = await getDownloadURL(imgSnapshot.ref);
                leagueData.img = url;
                await firebase.firestore().collection('QuizLeague').doc(id.toString()).set(leagueData);
                setLoading(false);
                navigate('/public-leagues');
            }
        } catch (error) {
            setLoading(false);
            console.log(error);
        }
    };

    const handleChange = (event) => {
        const {
            target: { value }
        } = event;

        setSelectedTopics(value);
    };

    useEffect(() => {
        const getSeasons = async () => {
            try {
                const seasonRef = firebase.firestore().collection('Seasons');
                const seasons = await seasonRef.orderBy('createAt', 'desc').get();
                setSeasons(seasons.docs.map((season) => season.data()));
            } catch (error) {
                console.log(error);
            }
        };

        const getTopicsAndSubTopics = async () => {
            try {
                const config = (await firebase.firestore().collection('Config').get()).docs.map((doc) => doc.data());
                setTopics(config[1].Topics);
            } catch (err) {
                console.log(err);
            }
        };

        getTopicsAndSubTopics();
        getSeasons();
    }, []);

    return (
        <>
            <Grid container direction="column" justifyContent="center" spacing={2}></Grid>
            {loading ? (
                <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center' }}>
                    <CircularProgress />
                </div>
            ) : (
                <form noValidate onSubmit={handleSubmit(onSubmit)} {...others}>
                    <Grid container spacing={matchDownSM ? 0 : 2}>
                        <Grid item xs={12} sm={6}>
                            <Controller
                                name="title"
                                control={control}
                                rules={{
                                    required: 'Quiz League title is required',
                                    minLength: {
                                        value: 5,
                                        message: 'League Title should be min 5 letters'
                                    }
                                }}
                                render={({ field }) => <TextField fullWidth label="Quiz League Title" margin="normal" {...field} />}
                            />
                            {errors.title && <span style={{ color: 'red' }}>{errors.title.message}</span>}
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <Controller
                                name="description"
                                control={control}
                                rules={{
                                    required: 'Quiz League Description is required',
                                    minLength: {
                                        value: 5,
                                        message: 'League Description should be min 5 letters'
                                    }
                                }}
                                render={({ field }) => <TextField fullWidth label="Quiz League description" margin="normal" {...field} />}
                            />
                            {errors.description && <span style={{ color: 'red' }}>{errors.description.message}</span>}
                        </Grid>
                    </Grid>
                    <Grid container spacing={matchDownSM ? 0 : 2}>
                        <Grid item xs={12} sm={6}>
                            <Controller
                                name="prizeMoney"
                                control={control}
                                rules={{ required: 'Prize money is required' }}
                                render={({ field }) => <TextField fullWidth label="Prize Money" margin="normal" {...field} />}
                            />
                            {errors.prizeMoney && <span style={{ color: 'red' }}>{errors.prizeMoney.message}</span>}
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <Controller
                                name="entryFee"
                                control={control}
                                rules={{ required: true }}
                                render={({ field }) => <TextField fullWidth label="Entry Fee" margin="normal" {...field} />}
                            />
                            {errors.entryFee && <span style={{ color: 'red' }}>Entry Fee is required</span>}
                        </Grid>
                    </Grid>
                    <Grid container spacing={matchDownSM ? 0 : 2}>
                        <Grid item xs={12} sm={6}>
                            <FormControl margin="normal" fullWidth>
                                <InputLabel id="demo-multiple-checkbox-label">Quiz League Topics</InputLabel>
                                <Select
                                    multiple
                                    labelId="demo-multiple-checkbox-label"
                                    id="demo-multiple-checkbox"
                                    value={selectedTopics}
                                    onChange={handleChange}
                                    label="Quiz League Topics"
                                    // input={<OutlinedInput label="Select Season" />}
                                >
                                    {topics.map((topic, index) => (
                                        <MenuItem key={index} value={topic.name}>
                                            {topic.name}
                                        </MenuItem>
                                    ))}
                                </Select>
                            </FormControl>

                            {topicsError && <span style={{ color: 'red' }}>Quiz league topic is required</span>}
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <FormControl margin="normal" fullWidth>
                                <InputLabel id="demo-multiple-checkbox-label">Select Season</InputLabel>
                                <Select
                                    value={seasonId}
                                    onChange={(e) => setSeasonId(e.target.value)}
                                    labelId="demo-multiple-checkbox-label"
                                    id="demo-multiple-checkbox"
                                    input={<OutlinedInput label="Select Season" />}
                                >
                                    {seasons.map((season, index) => {
                                        return (
                                            <MenuItem key={index} value={season.id}>
                                                {season.name}
                                            </MenuItem>
                                        );
                                    })}
                                </Select>
                            </FormControl>

                            {seasonIdError && <span style={{ color: 'red' }}>Season is required</span>}
                        </Grid>
                    </Grid>
                    <Grid container spacing={matchDownSM ? 0 : 2}>
                        <Grid item xs={12} sm={6} sx={{ marginTop: '15px' }}>
                            <Button
                                variant="outlined"
                                size="large"
                                sx={{ borderRadius: '10px', height: '52px' }}
                                fullWidth
                                onClick={() => setShowModal(true)}
                            >
                                Add Contact Info
                            </Button>
                            {infoError && <span style={{ color: 'red' }}>Please add Contact info</span>}
                        </Grid>
                        <Grid item xs={12} sm={6} sx={{ marginTop: '15px' }}>
                            <Button
                                component="label"
                                variant="outlined"
                                size="large"
                                sx={{ borderRadius: '10px', height: '52px' }}
                                fullWidth
                            >
                                Upload Cover Image
                                <input hidden accept="image/*" type="file" onChange={(e) => setImg(e.target.files[0])} />
                            </Button>
                            {imgError && <span style={{ color: 'red' }}>Cover Image is required</span>}
                        </Grid>
                        {img ? (
                            <Grid item sm={12} xs={12} md={12} sx={{ marginTop: '15px' }}>
                                <div>
                                    <img style={{ height: 150, width: '100%' }} alt="cover" src={URL.createObjectURL(img)} />
                                </div>
                            </Grid>
                        ) : (
                            <div></div>
                        )}
                    </Grid>
                    <Grid sx={{ display: 'flex', justifyContent: 'center', marginTop: '15px' }}>
                        <AnimateButton>
                            <Button
                                disableElevation
                                sx={{ width: 200, background: '#B92136' }}
                                size="large"
                                type="submit"
                                variant="contained"
                            >
                                Create
                            </Button>
                        </AnimateButton>
                    </Grid>
                </form>
            )}

            <Dialog open={showModal} onClose={() => setShowModal(false)}>
                <DialogTitle>Add Contact Info</DialogTitle>
                <DialogContent>
                    <Grid container spacing={matchDownSM ? 0 : 2}>
                        <Grid item xs={12} sm={6}>
                            <Controller
                                name="firstName"
                                control={control}
                                rules={{ required: true }}
                                render={({ field }) => <TextField fullWidth label="First Name" margin="normal" {...field} />}
                            />
                            {errors.firstName && <span style={{ color: 'red' }}>First Name is required</span>}
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <Controller
                                name="lastName"
                                control={control}
                                rules={{ required: true }}
                                render={({ field }) => <TextField fullWidth label="Last Name" margin="normal" {...field} />}
                            />
                            {errors.lastName && <span style={{ color: 'red' }}>Last Name is required</span>}
                        </Grid>
                    </Grid>
                    <Grid container spacing={matchDownSM ? 0 : 2}>
                        <Grid item xs={12} sm={6}>
                            <Controller
                                name="email"
                                control={control}
                                rules={{ required: true }}
                                render={({ field }) => <TextField fullWidth label="Email" margin="normal" {...field} />}
                            />
                            {errors.email && <span style={{ color: 'red' }}>email is required</span>}
                        </Grid>
                        <Grid item xs={12} sm={6} sx={{ marginTop: '12px' }}>
                            <Controller
                                name="phone"
                                control={control}
                                rules={{ required: true }}
                                render={({ field }) => (
                                    <PhoneInput country="us" specialLabel="Phone Number" inputStyle={{ width: '100%' }} {...field} />
                                )}
                            />

                            {errors.phone && <span style={{ color: 'red' }}>Phone Number is required</span>}
                        </Grid>
                    </Grid>
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => setShowModal(false)}>Cancel</Button>
                    <Button onClick={() => setShowModal(false)}>Add</Button>
                </DialogActions>
            </Dialog>
        </>
    );
};

export default SubForm;
